ul.order li {
  border-bottom: 1px solid #000;
  padding: 1.5rem 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 1.4rem;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: flex-start;
}

ul.order li:last-child {
  margin-bottom: 10px;
}

ul.order li:hover button {
  display: inline;
}
ul.order li button {
  border: 0;
  display: none;
  line-height: 1;
  padding: 0;
  margin-left: 5px;
}
ul.order li.total {
  font-weight: 600;
  border-bottom: 3px solid #000;
  border-top: 3px double #000;
}

.total {
  display: flex;
  justify-content: space-between;
}

ul.order li.unavailable {
  text-decoration: line-through;
  background: #f8d0d2;
}
ul.order li .price {
  font-size: 1.2rem;
  display: inline-block;
  margin-left: 5px;
}
ul.order li span.count {
  position: relative;
  overflow: hidden;
}

.order-title {
  text-align: center;
}

.order-enter {
  -webkit-transform: translateX(-120%);
  transform: translateX(-120%);
  -webkit-transition: 0.5s;
  transition: 0.5s;
  max-height: 0;
  padding: 0 !important;
}
.order-enter.order-enter-active {
  max-height: 60px;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  padding: 2rem 0 !important;
}
.order-exit {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  -webkit-transform: translateX(0);
  transform: translateX(0);
}
.order-exit.order-exit-active {
  -webkit-transform: translateX(120%);
  transform: translateX(120%);
  padding: 0;
}
.count-enter {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
}
.count-enter.count-enter-active {
  -webkit-transform: translateY(0);
  transform: translateY(0);
}
.count-exit {
  -webkit-transform: translateY(0);
  transform: translateY(0);
  -webkit-transition: 0.5s;
  transition: 0.5s;
  position: absolute;
  left: 0;
  bottom: 0;
}
.count-exit.count-exit-active {
  background: #008000;
  -webkit-transform: translateY(-100%) scale(3);
  transform: translateY(-100%) scale(3);
}

.list-of-fish {
  border-top: 2px solid #000;
  border-bottom: 1px solid #000;
  padding-top: 5px;
  margin-top: 2rem;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
